import cmdCellItem from '@/components/cmd-cell-item/cmd-cell-item.vue';
export default {
	components: {
		cmdCellItem
	},
	data() {
		return {
			state:0,
			addon: '请选择',
			list: [
				{
					name: '姓名',
					plac: '请输入姓名'
				},
				{
					name: '手机号',
					plac: '请输入租客电话'
				},
				{
					name: '身份证号',
					plac: '请输入租客身份证号'
				}
			],
			data: []
		};
	},
	onLoad() {
		if(this.state == 1){
			uni.setNavigationBarTitle({
				title: '同住人'
			});
		}
	},
	methods: {
		goDetailPage() {
			var itemList = ['配偶', '父母', '子女', '祖父母', '子孙女', '兄弟姐妹', '其它亲属', '朋友', '其它'];
			uni.showActionSheet({
				itemList: itemList,
				success: e => {
					this.addon = itemList[e.tapIndex];
				}
			});
		}
	}
};
